/*  */
.training-table {
    width: auto; /*  */
    margin: 20px auto; /* 20px */
    border-collapse: collapse; /*  */
    font-size: 18px; /*  */
}

.training-delete-button {
    background-color: rgba(255, 0, 0, 0.5); /* 50% */
    color: white; /*  */
    transition: background-color 0.3s ease; /*  */
  }
  
  .training-delete-button:hover {
    background-color: red; /*  */
    cursor: pointer; /*  */
  }


/*  */
@media screen and (max-width: 768px) {
  .training-table {
    width: 100%;
    margin: 10px 0;
    font-size: 16px;
    overflow-x: visible;
    white-space: normal;
    table-layout: auto;
  }

  .training-delete-button {
    width: 25px;
    height: 25px;
    padding: 4px;
    font-size: 14px;
  }

  .completion-cell {
    width: 40px !important;
    min-width: 40px !important;
    padding: 3px !important;
  }
  
  .operation-cell {
    width: 60px !important;
    min-width: 60px !important;
    padding: 3px !important;
  }
}


@media screen and (max-width: 480px) {
  .training-table {
    table-layout: fixed;
    width: 100%;
    max-width: 100%;
    overflow-x: visible;
  }
  
  .training-table th,
  .training-table td {
    padding: 6px 3px;
    font-size: 14px;
  }

  .training-delete-button {
    width: 22px;
    height: 22px;
    padding: 3px;
    font-size: 12px;
  }

  .completion-cell {
    width: 35px !important;
    min-width: 35px !important;
    padding: 2px !important;
  }
  
  .operation-cell {
    width: 50px !important;
    min-width: 50px !important;
    padding: 2px !important;
  }
}