/*  */
.add-set-button-container {
    text-align: center; /*  */
    margin-top: 20px; /*  */
}

/*  */
.add-set-button {
    background-color: rgba(162, 217, 255, 0.2); /* 50% */
    border: 2px solid rgba(162, 217, 255, 0.2); /* 20% */
    border-radius: 5px; /*  */
    padding: 15px 30px; /*  */
    cursor: pointer; /*  */
    font-weight: bold; /*  */
    color: black; /*  */
    transition: background-color 0.3s ease; /*  */
    font-size: 18px; /*  */
    border: 2px solid rgba(162, 217, 255, 0.8); /*  */
}

/*  */
.add-set-button:hover {
    background-color: rgba(130, 192, 255, 0.5); /* 50% */
}