/*  */
.part-selector {
    display: flex; /*  */
    justify-content: space-evenly; /*  */
    margin-bottom: 10px; /*  */
  }
  
  /*  */
  .part-button {
    padding: 10px 15px; /*  */
    border: none; /*  */
    border-radius: 20px; /*  */
    background: rgba(162, 217, 255, 0.5); /*  */
    color: #000; /*  */
    font-size: 16px; /*  */
    font-weight: bold; /*  */
    cursor: pointer; /*  */
    transition: background-color 0.3s ease, color 0.3s ease; /*  */
  }
  
  /*  */
  .part-button:hover {
    background: #b2ebf2; /*  */
    color: #0056b3; /*  */
  }
  
  /*  */
  .part-button.active {
    background: #007bff; /*  */
    color: white; /*  */
  }