/*  */
.exercise-list {
  max-height: 400px;
  overflow-y: auto; /*  */
  display: flex; /*  */
  flex-direction: column; /*  */
  align-items: center; /*  */
  width: 100%; /*  */
}

/*  */
.exercise-item {
  width: 85%;
  text-align: center; /*  */
  padding: 10px; /*  */
  margin: 5px 0; /*  */
  border: 1px solid #b2ebf2; /*  */
  border-radius: 10px; /*  */
  background: #fff; /*  */
  font-size: 14px; /*  */
  color: #000; /*  */
  cursor: pointer; /*  */
  transition: background-color 0.3s ease, color 0.3s ease; /*  */
}

/*  */
.exercise-item:hover {
  background: #e0f7fa; /*  */
  color: #007bff; /*  */
}

/* 480px */
@media (max-width: 480px) {
  .exercise-list {
    padding: 0 5px; /*  */
  }
  
  .exercise-item {
    width: 95%; /*  */
    font-size: 13px; /*  */
    padding: 12px 8px; /*  */
  }
}