/*  */
.calculator-button {
    background-color: rgba(162, 217, 255, 0.1);
    border: 1px solid #a2d9ff;
    border-radius: 5px;
    padding: 20px; /*  */
    font-size: 24px; /*  */
    margin: 0; /*  */
    cursor: pointer;
    width: 80px; /*  */
    color: rgba(0, 0, 255, 0.7);
  }
  
  .calculator-button:hover {
    background-color: #a2d9ff;
  }
  
  /*  */
  .calculator-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* 3 */
    gap: 5px; /*  */
    justify-items: center;
  }
  
  .calculator-grid-extended {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* 2 */
    gap: 5px; /*  */
    justify-items: center;
    margin-top: 10px; /*  */
  }