/*  */
.react-calendar {
    width: 100%; /* 100% */
    max-width: 600px; /* 600px */
    background-color: rgba(173, 216, 230, 0.001); /*  */
    border: 1px solid #ddd; /*  */
    border-radius: 8px; /*  */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /*  */
    font-family: 'Helvetica Neue', Arial, sans-serif; /*  */
    line-height: 18px; /*  */
    margin: 20px auto; /* 20px */
    padding: 20px; /* 20px */
    margin-top: 100px; /*  */
}

/*  */
.react-calendar__month-view__weekdays__weekday {
    text-align: center;
    font-weight: bold;
    text-decoration: none;
    font-size: 20px;
    padding: 10px 0;
    color: #000; /*  */
}

/*  */
.react-calendar__month-view__weekdays__weekday:nth-child(1) abbr {
    color: #000;
}

/*  */
.react-calendar__month-view__weekdays__weekday:nth-child(6) abbr {
    color: blue;
}

/*  */
.react-calendar__month-view__weekdays__weekday:nth-child(7) abbr {
    color: red;
}