/*  */
.training-table button {
    border: none; /*  */
    border-radius: 5px; /*  */
    padding: 10px 15px; /*  */
    width: 80px; /*  */
    height: 40px; /*  */
    font-size: 16px; /*  */
}

/*  */
.training-table .complete-button {
    background-color: rgba(162, 217, 255, 0.2); /*  */
    color: black; /*  */
    cursor: pointer; /*  */
}

/*  */
.training-table .incomplete-button {
    background-color: white; /*  */
    color: white; /*  */
    border: 2px solid rgba(162, 217, 255, 0.5); /*  */
    cursor: pointer; /*  */
}


@media screen and (max-width: 768px) {
    .training-table button {
        width: 50px !important; /* 50px */
        padding: 8px 5px; /*  */
        font-size: 14px; /*  */
    }
    
    .training-table .complete-button,
    .training-table .incomplete-button {
        width: 50px !important; /* 50px */
    }
}


@media screen and (max-width: 480px) {
    .training-table button {
        width: 50px !important; /* 50px */
        height: 35px; /*  */
        padding: 5px 3px; /*  */
        font-size: 12px; /*  */
    }
}