/*  */
.header-menu-overlay {
  position: fixed; /*  */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); /*  */
  display: flex;
  justify-content: center; /*  */
  align-items: center; /*  */
  z-index: 1000; /*  */
}

/*  */
.header-menu-modal {
  background: #f0f8ff; /*  */
  padding: 20px; /*  */
  border-radius: 15px; /*  */
  width: 500px; /*  */
  max-width: 90%; /*  */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /*  */
  border: 2px solid #b2ebf2; /*  */
  text-align: center; /*  */
  animation: fadeIn 0.3s ease; /*  */
  display: flex;
  flex-direction: column;
  max-height: 80vh; /* 80% */
  overflow-y: auto; /*  */
  -webkit-overflow-scrolling: touch; /* Safari */
}

@media (max-width: 768px) {
  .header-menu-modal {
    padding: 15px;
    max-height: 70vh;
  }
}

/*  */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1; /*  */
    transform: scale(1); /*  */
  }
}

/*  */
.header-menu-list {
  list-style: none; /*  */
  padding: 0;
  margin: 0 0 15px 0;
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
}

/*  */
.header-menu-list li {
  padding: 12px 0; /*  */
  margin: 5px 0; /*  */
  border-bottom: 1px solid #b2ebf2; /*  */
  cursor: pointer; /*  */
  transition: background-color 0.3s, color 0.3s; /*  */
  font-size: 18px; /*  */
  font-weight: bold; /*  */
  border-radius: 10px; /*  */
  color: #000;
  width: 100%;
  box-sizing: border-box;
  appearance: none;
}

@media (max-width: 768px) {
  .header-menu-list li {
    padding: 10px 0;
    font-size: 16px;
  }
}

/*  */
.header-menu-list li:hover {
  background: #e0f7fa; /*  */
  color: #007bff; /*  */
}

/*  */
.header-close-button {
  display: block;
  width: auto;
  margin: 10px auto;
  padding: 10px 25px; /*  */
  background: #007bff; /*  */
  color: white; /*  */
  border: none; /*  */
  border-radius: 20px; /*  */
  cursor: pointer; /*  */
  font-size: 18px; /*  */
  transition: background-color 0.3s ease, transform 0.3s ease; /*  */
  appearance: none;
}

/*  */
.header-close-button:hover {
  background: #0056b3; /*  */
  transform: scale(1.05); /*  */
}

/*  */
.header-menu-list a {
  text-decoration: none; /*  */
  color: inherit; /*  */
  display: block;
  width: 100%;
  height: 100%;
}

/*  */
.header-menu-list a:visited {
  color: inherit; /*  */
}



@media (max-width: 480px) {
  .header-menu-modal {
    padding: 10px;
    max-height: 80vh;
    max-width: 95%;
  }
  
  .header-menu-list li {
    padding: 8px 0;
    font-size: 14px;
    margin: 3px 0;
  }
  
  .header-close-button {
    padding: 8px 20px;
    font-size: 16px;
  }
}