.weight-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .weight-modal {
    background: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .weight-modal-display {
    font-size: 24px;
    padding: 10px;
    width: calc(100% - 20px);
    margin-bottom: 20px;
    text-align: center; /*  */
    border: 1px solid #a2d9ff; /*  */
    border-radius: 10px; /*  */
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .fixed-size {
    height: 4vh; /*  */
    overflow: hidden; /*  */
  }
  
  .weight-modal-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* 3 */
    gap: 5px; /*  */
    justify-items: center;
  }
  
  .weight-modal-button {
    background-color: rgba(162, 217, 255, 0.1);
    border: 1px solid #a2d9ff;
    border-radius: 5px;
    padding: 20px; /*  */
    font-size: 24px; /*  */
    margin: 0; /*  */
    cursor: pointer;
    width: 80px; /*  */
    color: rgba(0, 0, 255, 0.7);
  }

  .weight-modal-button:hover {
    background-color: #a2d9ff;
  }
  
  .weight-modal-preservation-button {
    background-color: #87CEFA; /*  */
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
    margin-top: 10px;
    grid-column: span 3; /* 3 */
    justify-self: center; /*  */
  }

  .weight-modal-preservation-button:hover {
    background-color: #00BFFF;
  }