/*  */
.training-info {
  background-color: #f0f8ff;
  padding: 20px; /*  */
  border-radius: 5px;
  text-align: center;
  font-size: 18px; /*  */
  border: 2px solid #a2d9ff; /*  */
  max-width: 600px; /* 600px */
  margin: 0 auto; /*  */
  margin-top: 40px;
}

/*  */
.training-info h1,
.training-info h2,
.training-info h3 {
  font-size: 28px; /*  */
  margin-bottom: 10px; /*  */
  font-weight: bold; /*  */
}

/*  */
.training-info p {
  font-size: 20px; /*  */
  margin-bottom: 10px; /*  */
  font-weight: bold; /*  */
}

/*  */
.training-info ul,
.training-info ol {
  font-size: 20px; /*  */
  margin-bottom: 10px; /*  */
  font-weight: bold; /*  */
}

/*  */
.training-info li {
  margin-bottom: 5px; /*  */
}

/*  */
.training-info .bench-press {
  display: inline-block; /*  */
  font-size: 24px; /*  */
  font-weight: bold; /*  */
  text-align: center; /*  */
  border: 2px solid #a2d9ff; /*  */
  padding: 10px; /*  */
  border-radius: 5px; /*  */
  margin-left: 10px; /*  */
  margin: 20px 0; /*  */
}

/*  */
.training-info .exercise-display {
  font-size: 20px; /*  */
  font-weight: bold; /*  */
  text-align: center; /*  */
  border: 2px solid #a2d9ff; /*  */
  padding: 10px; /*  */
  border-radius: 5px; /*  */
  background-color: #ffffff; /*  */
  width: auto; /*  */
  box-sizing: border-box; /*  */
  cursor: pointer; /*  */
}


.calories-info {
  position: relative;
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: bold;
}

/*  */
.calories-value {
  font-weight: 600;
  color: #ff0000;
  margin-left: 5px;
}

/*  */
.calories-tooltip {
  display: block;
  font-size: 12px;
  color: #999;
  margin-top: 2px;
  font-weight: normal;
}



@media screen and (max-width: 600px) {
  .training-info .exercise-display {
    font-size: 16px;
  }
  
  .training-info {
    padding: 15px;
  }
  
  .training-info p {
    font-size: 18px;
  }
}