/*  */
.react-calendar__tile {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 100%;
    text-align: center;
    padding: 10px;
    background: none;
    border: none;
    color: #000;
    font-size: 14px;
    transition: background-color 0.3s ease;
    flex: 1;
    margin: 0;
    border-radius: 50%;
    width: 40px;
    height: 40px;
}

/*  */
.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
    background-color: #e0f7fa;
    border-radius: 50%;
    color: #000;
    width: 40px;
    height: 40px;
    border: 2px solid rgba(162, 217, 255, 0.5);
}

/*  */
.react-calendar__tile--now {
    background: #ffff00;
    color: #000;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    border: 2px solid #ffff00;
}

/*  */
.react-calendar__tile--now:enabled:hover,
.react-calendar__tile--now:enabled:focus {
    background: #a9d4ff;
    width: 40px;
    height: 40px;
    border: 2px solid rgba(162, 217, 255, 0.5);
}

/*  */
.react-calendar__tile--hasActive {
    background: #A2D9FF;
    width: 40px;
    height: 40px;
}

/*  */
.react-calendar__tile--hasActive:enabled:hover,
.react-calendar__tile--hasActive:enabled:focus {
    background: #a9d4ff;
    width: 40px;
    height: 40px;
}

/*  */
.react-calendar__tile--sunday {
    color: red; /*  */
}

/*  */
.react-calendar__tile--saturday {
    color: blue; /*  */
}

/*  */
.react-calendar__tile--active {
    background: rgba(173, 216, 230, 0.3);
    color: #000;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    border: 2px solid #a9d4ff;
}

/*  */
.react-calendar__month-view__days__day {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px;
    border-radius: 50%;
    transition: background-color 0.3s ease;
    font-size: 20px;
    font-weight: bold;
    flex: 1;
    width: 40px;
    height: 40px;
}

/*  */
.react-calendar__month-view__days__day--neighboringMonth {
    color: #ccc;
}

/*  */
.react-calendar__year-view__months__month,
.react-calendar__decade-view__years__year,
.react-calendar__century-view__decades__decade {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 19px;
    color: #000;
    font-weight: bold;
    padding: 10px;
    width: 80px; /*  */
    height: 80px; /*  */
}

/*  */
.react-calendar__year-view__months__month:enabled:hover,
.react-calendar__year-view__months__month:enabled:focus,
.react-calendar__decade-view__years__year:enabled:hover,
.react-calendar__decade-view__years__year:enabled:focus,
.react-calendar__century-view__decades__decade:enabled:hover,
.react-calendar__century-view__decades__decade:enabled:focus {
    background-color: #e0f7fa;
    color: #000;
    width: 80px; /*  */
    height: 80px; /*  */
}

/*  */
.react-calendar__tile abbr[aria-label$=""] {
    display: none;
}

/*  */
.react-calendar__tile--has-training {
    background-color: #d0e7ff;
    color: #000;
    border-radius: 50%;
    border: 2px solid #b0d4ff;
}

/*  */
.react-calendar__tile--has-training:enabled:hover,
.react-calendar__tile--has-training:enabled:focus {
    background-color: #b0d4ff;
    border: 2px solid #90c1ff;
}

/*  */
.react-calendar__tile--now.react-calendar__tile--has-training {
    background: linear-gradient(135deg, #ffff00 50%, #d0e7ff 50%);
    border: 2px solid #ffff00;
}

/*  */
.react-calendar__tile--active.react-calendar__tile--has-training {
    background: linear-gradient(135deg, rgba(173, 216, 230, 0.7) 50%, rgba(208, 231, 255, 0.7) 50%);
    border: 2px solid #b0d4ff;
}